import React, { Component } from "react";
import { connect } from "react-redux";
import { reduxForm, reset } from "redux-form";
import axios from "axios";
import FormTransaksiPenjualan from "./Component/FormTranskasiPenjualan";
import SelectSearch from "react-select-search";
import { getUUID } from "../actions/fakturAction";
import Swal from "sweetalert2";
import { getListTransaksiPenjualan } from "../actions/transaksiPenjualanAction";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import {
  getBruto,
  getGross,
  getK,
  getNetto,
  getP,
  getPkg,
  getQty,
  getRp,
} from "../actions/LogicAction";
import PrintNota from "../Stocking/Component/PrintNota";
import { warning } from "../Alert";
// import { Link } from "react-router-dom";
import { deleteIndex } from "../deleteLocalStorage";
import ReactTooltip from "react-tooltip";
import { getTotalNW } from "../actions/returnPenjualanAction";

const maptostate = (state) => {
  return {
    noFaktur: state.provinsi.noFaktur,
    transaksiPenjualan: state.provinsi.transaksiPenjualan,
    listCustomer: state.provinsi.customer,
    listLokasi: state.provinsi.lokasi,
    listSales: state.provinsi.salesman,
    totalNW: state.provinsi.totalNW,
    sumNetto: state.perhitungan.sumNetto,
    sumBruto: state.perhitungan.sumBruto,
    sumPkg: state.perhitungan.sumPkg,
    sumP: state.perhitungan.sumP,
    sumK: state.perhitungan.sumK,
    sumQty: state.perhitungan.sumNetto,
    sumGross: state.perhitungan.sumGross,
    sumRp: state.perhitungan.sumRp,
    username: state.provinsi.username,
  };
};
const server = process.env.REACT_APP_BACKEND_URL;
const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class TransaksiPenjualan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listLokasi: [],
      listSales: [],
      listCustomer: [],
      columns: [
        {
          dataField: "kodeJenis",
          text: "Kode Barang",
        },
        {
          dataField: "kadarCetak",
          text: "Kadar Cetak(%)",
        },
        {
          dataField: "p",
          text: "P",
        },
        {
          dataField: "k",
          text: "K",
        },
        {
          dataField: "pkg",
          text: "Bungkus(Gr)",
        },
        {
          dataField: "bruto",
          text: "Berat(Gr)",
        },
        {
          dataField: "kadarTransaksi",
          text: "Harga(%)",
        },
        {
          dataField: "netto",
          text: "Murni(Gr)",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "discGlobalPr",
          text: "%",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "discGlobalGr",
          text: "Disc (Gr)",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "nettoDiscGlobalExt",
          text: "Netto Disc(Gr)",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger mr-2"
                  onClick={() =>
                    deleteIndex(
                      "transaksiPenjualan",
                      rowIndex,
                      this.props.dispatch,
                      getListTransaksiPenjualan()
                    )
                  }
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash mr-1"></i>
                </button>
                <br />
                {/* <button
                  data-tip
                  data-for="edit"
                  className="btn btn-warning mr-2"
                  data-toggle="modal"
                  data-target="#editModal"
                  onClick={() => this.setEdit(row)}
                >
                  <ReactTooltip id="edit" type="dark" effect="solid">
                    <span>Edit</span>
                  </ReactTooltip>
                  <i className="fa fa-edit mr-1"></i>
                </button> */}
              </div>
            );
          },
        },
      ],
      discGlobalGr: 0,
      discGlobalPr: 0,
      totalNWExt: 0,
      noBon: "",
      namaCustomer: "",
      lokasi: "",
      sales: "",
      namaToko: "",
      tanggal: "",
      data: "",
    };
  }

  componentDidMount() {
    axios.get(server + "storages2?tipeData=LOKASI").then((res) =>
      this.setState({
        listLokasi: res.data,
      })
    );
    axios.get(server + "storages2?tipeData=SALES").then((res) =>
      this.setState({
        listSales: res.data,
      })
    );
    axios.get(server + "customers").then((res) =>
      this.setState({
        listCustomer: res.data,
      })
    );
    this.props.dispatch(getUUID());
    this.props.dispatch(getListTransaksiPenjualan());
    this.props.dispatch(getTotalNW());
    this.props.dispatch(getBruto("transaksiPenjualan"));
    this.props.dispatch(getNetto("transaksiPenjualan"));
    this.props.dispatch(getPkg("transaksiPenjualan"));
    this.props.dispatch(getRp("transaksiPenjualan"));
    this.props.dispatch(getGross("transaksiPenjualan"));
    this.props.dispatch(getP("transaksiPenjualan"));
    this.props.dispatch(getK("transaksiPenjualan"));
    this.props.dispatch(getQty("transaksiPenjualan"));
  }

  handleSubmit(data) {
    var barang = data.kodeBarang
      ? data.kodeBarang
      : "DEFAULT|DEFAULT|DEFAULT|DEFAULT";
    var result_explode = barang.split("|");
    var simpan = {
      noFaktur: this.props.noFaktur, //
      nettoDiscGlobalExt: data.nettoDiscGlobalExt,
      gross: parseFloat(data.gross), //berat + bungkus
      netto: parseFloat(data.netto), //murni
      qty: 1,
      k: parseFloat(data.k),
      rp: parseFloat(data.rp === undefined ? 0 : data.rp), //ongkos
      kadarCetak: result_explode[2],
      keterangan: data.keterangan,
      discGlobalPr: data.discGlobalPr,
      kodeKategori: "-",
      kadarTransaksi: parseFloat(data.kadarTransaksi), //harga%
      kodeJenis: result_explode[0], //kodebarang
      noBarcode: "-",
      p: parseFloat(data.p),
      discGlobalGr: data.discGlobalGr,
      pkg: parseFloat(data.pkg),
      bruto: parseFloat(data.bruto), //berat
      nettoDiscGlobal: 0,
    };
    var array = JSON.parse(localStorage.getItem("transaksiPenjualan")) || [];
    array.push(simpan);
    var GrandTotalNetto = array
      .map((list) => parseFloat(list.nettoDiscGlobalExt))
      .reduce((a, b) => a + b, 0)
      .toFixed(3);
    localStorage.setItem("GrandTotalNetto", GrandTotalNetto);
    localStorage.setItem("transaksiPenjualan", JSON.stringify(array));
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
    this.props.dispatch(getListTransaksiPenjualan());
    this.props.dispatch(getTotalNW());
    this.props.dispatch(reset("FormTransaksiPenjualan"));
    document.getElementById("dismissModal").click();
    this.props.dispatch(getNetto("transaksiPenjualan"));
  }

  transaksiPenjualanSubmit() {
    if (localStorage.getItem("transaksiPenjualan") === null) {
      warning("Barang masih kosong, Silahkan tambah barang");
      return false;
    } else if (this.state.noBon === "") {
      warning("Mohon isi Nomor bon");
      return false;
    } else if (this.state.lokasi === "") {
      warning("Mohon pilih lokasi");
      return false;
    } else if (this.state.sales === "") {
      warning("Mohon pilih Sales");
      return false;
    } else if (this.state.namaToko === "") {
      warning("Mohon pilih toko");
      return false;
    } else if (this.state.discGlobalPr === 0) {
      warning("Mohon isi Discount");
      return false;
    } else if (this.state.discGlobalGr === 0) {
      warning("Mohon isi Discount Gr");
      return false;
    } else if (this.state.totalNWExt === 0) {
      warning("Mohon isi Grand Total");
      return false;
    } else if (this.state.tanggal === "") {
      warning("Mohon isi Tanggal");
      return false;
    }
    var headDiscount = {
      totalNettoExt: this.state.totalNWExt, // grand total NW ext
      totalNettoFaktur: this.props.gross, // total NW
      discPr: this.state.discGlobalPr, //dic global
      discGr: this.state.discGlobalGr, //gramasi dic global ( disc global x bruto )
      totalNettoAkhir:
        parseFloat(this.props.gross) - parseFloat(this.state.discGlobalGr), // grand total NW
    };
    var footerItem = {
      totalBruto: this.props.sumBruto,
      totalNetto: this.props.sumNetto,
      totalNettoExt: 0, // penjualan tidakada
      totalGross: this.props.sumGross,
      totalRp: this.props.sumRp,
      totalQty: this.props.sumQty,
      totalK: this.props.sumK,
      totalP: this.props.sumP,
      totalpkg: this.props.sumPkg,
    };
    var simpan = {
      noBon: this.state.noBon,
      noFaktur: this.props.noFaktur,
      tanggal: this.state.tanggal,
      kodeLokasi: this.state.lokasi,
      kodeSales: this.state.sales,
      namaToko: this.state.namaToko,
      kodeCustomer: this.state.kodeCustomer,
      barangDetails: JSON.parse(localStorage.getItem("transaksiPenjualan")),
      headDiscount: headDiscount,
      footerItem: footerItem,
      footerDocument: {
        inputBy: this.props.username, //INPUT BY DEFAULT
      },
    };
    console.log(JSON.stringify(simpan));
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = JSON.parse(localStorage.getItem("transaksiPenjualan"));
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        ++i,
        ticket.kadarCetak,
        ticket.kodeJenis,
        ticket.keterangan,
        ticket.bruto,
        ticket.kadarTransaksi,
        ticket.discGlobalGr,
        ticket.netto,
        ticket.pkg,
        ticket.qty,
        parseFloat(ticket.rp).toLocaleString("id-ID"),
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    const footer = [
      "",
      "",
      "",
      "",
      table
        .map((list) => parseFloat(list.bruto))
        .reduce((a, b) => a + b, 0)
        .toFixed(3),
      "",
      table
        .map((list) => parseFloat(list.discGlobalGr))
        .reduce((a, b) => a + b, 0)
        .toFixed(3),
      table
        .map((list) => parseFloat(list.netto))
        .reduce((a, b) => a + b, 0)
        .toFixed(3),
      table.map((list) => parseFloat(list.pkg)).reduce((a, b) => a + b, 0),
      table.map((list) => parseFloat(list.qty)).reduce((a, b) => a + b, 0),
      table
        .map((list) => parseFloat(list.rp))
        .reduce((a, b) => a + b, 0)
        .toLocaleString("id-ID"),
    ];
    tableRows.push(footer);
    console.log(JSON.stringify(tableRows));
    var columTable = [
      "No",
      "Kadar",
      "Kode",
      "Keterangan",
      "Bruto (Gr)",
      "Harga(%)",
      "Diskon",
      "Netto(Gr)",
      "Pkg",
      "Qty",
      "Ongkos(Rp)",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    axios
      .post(serverNonMaster + "cst/dc/pjl", simpan)
      .then(() =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() =>
            PrintNota(
              "no Trx",
              this.props.noFaktur.toUpperCase(),
              "Tanggal",
              this.state.tanggal,
              "No Bon",
              this.state.noBon.toUpperCase(),
              "Sales",
              this.state.namaToko.toUpperCase(),
              "ADMIN",
              this.state.tanggal,
              "",
              columTable,
              "Transaksi Penjualan",
              tableRows
            )
          )
          .then(() => localStorage.removeItem("transaksiPenjualan"))
          .then(() => localStorage.removeItem("GrandTotalNetto"))
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() => this.props.dispatch(getUUID()))
          .then(() => this.props.dispatch(getListTransaksiPenjualan()))
          .then(() =>
            this.setState({
              noBon: "",
              namaCustomer: "",
              lokasi: "",
              sales: "",
              namaToko: "",
              tanggal: "",
            })
          )
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        })
      );
    console.log(JSON.stringify(simpan));
  }
  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  inputGrandTotal = (input) => (e) => {
    var GrandNetto = this.props.sumGross - parseFloat(this.state.discGlobalGr);
    var grandTotal = e.target.value;
    console.log(GrandNetto + "-" + grandTotal);
    if (GrandNetto - grandTotal < -1) {
      warning("Selisih lebih dari 1");
      e.target.value = "";
      return false;
    } else {
      if (GrandNetto - grandTotal <= 1) {
        this.setState({
          [input]: e.target.value,
        });
      } else {
        warning("Selisih lebih dari 1");
        e.target.value = "";
        return false;
      }
    }
  };
  discount = (input) => (e) => {
    var hasil =
      (parseFloat(e.target.value) / 100) *
      parseFloat(localStorage.getItem("GrandTotalNetto"));
    this.setState({
      discGlobalGr: hasil.toFixed(3),
      discGlobalPr: e.target.value,
      GrandTotalNetto: (
        parseFloat(localStorage.getItem("GrandTotalNetto")) - hasil.toFixed(3)
      ).toFixed(3),
    });
  };
  onSubmit(data, input) {
    this.setState({
      [input]: data,
    });
  }
  onSubmitToko(data, input) {
    var split = data.split("|");
    this.setState({
      kodeCustomer: split[0],
      namaCustomer: split[1],
      namaToko: split[2],
    });
  }

  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12">
          <div className="card">
            <div className="card-body">
              <div className="row">
                <div className="col-lg-9">
                  <h1>Transaksi Penjualan</h1>
                </div>
                {/* <div className="col-lg-3">
                  <div className="text-right">
                    <Link to="/ayugold.com/EditForm/transaksiPenjualan">
                      <button
                        className="btn btn-warning"
                        // data-target="#editModal"
                        // data-toggle="modal"
                      >
                        Edit Transaksi Penjualan
                      </button>
                    </Link>
                  </div>
                </div> */}
                <div className="col-lg-12">
                  <br />
                </div>
                <div className="col-lg-4 col-md-4">
                  <div className="form-group">
                    <label htmlFor="">Tanggal</label>
                    <input
                      name="tanggal"
                      type="date"
                      label="Tanggal"
                      className="form-control"
                      onChange={this.inputChange("tanggal")}
                      value={this.state.tanggal}
                    />
                  </div>
                </div>
                <div className="col-lg-4 col-md-4">
                  <div className="form-group">
                    <label htmlFor="">No Bon</label>
                    <input
                      name="noBon"
                      type="text"
                      label="No Bon"
                      className="form-control"
                      onChange={this.inputChange("noBon")}
                      value={this.state.noBon}
                    />
                  </div>
                </div>
                <div className="col-lg-4 col-md-4"></div>
                <div className="col-lg-3 col-md-3">
                  <div className="form-group">
                    <label htmlFor="">Lokasi</label>
                    <SelectSearch
                      options={this.state.listLokasi.map((obj) => {
                        var payload = {
                          value: obj.kodeLokasi,
                          name: obj.namaLokasi,
                        };
                        return payload;
                      })}
                      search
                      placeholder="Silahkan Pilih"
                      onChange={(data) => this.onSubmit(data, "lokasi")}
                      value={this.state.lokasi}
                    />
                  </div>
                </div>
                <div className="col-lg-3 col-md-3">
                  <div className="form-group">
                    <label htmlFor="">Sales</label>
                    <SelectSearch
                      options={this.state.listSales.map((obj) => {
                        var payload = {
                          value: obj.kodeLokasi,
                          name: obj.namaLokasi,
                        };
                        return payload;
                      })}
                      search
                      placeholder="Silahkan Pilih"
                      onChange={(data) => this.onSubmit(data, "sales")}
                      value={this.state.sales}
                    />
                  </div>
                </div>
                <div className="col-lg-3 col-md-3">
                  <div className="form-group">
                    <label htmlFor="">Nama Toko</label>
                    <SelectSearch
                      options={this.state.listCustomer.map((obj) => {
                        var payload = {
                          value:
                            obj.kodeCustomer +
                            "|" +
                            obj.namaCustomer +
                            "|" +
                            obj.namaToko,
                          name: obj.namaToko,
                        };
                        return payload;
                      })}
                      search
                      placeholder="Silahkan Pilih"
                      onChange={(data) => this.onSubmitToko(data, "namaToko")}
                    />
                  </div>
                </div>
                <div className="col-lg-3 col-md-3">
                  <div className="form-group">
                    <label htmlFor="">Customer</label>
                    <input
                      name="customer"
                      type="text"
                      className="form-control"
                      label="Customer"
                      onChange={this.inputChange("namaCustomer")}
                      defaultValue={this.state.namaCustomer}
                    />
                  </div>
                </div>
                <div className="col-lg-3 mb-3">
                  <label htmlFor="">Total NW</label>
                  <input
                    name="totalNW"
                    type="number"
                    className="form-control"
                    min={0}
                    step={0.01}
                    label="totalNW"
                    value={this.props.totalNW}
                    readOnly={true}
                  />
                </div>
                <div className="col-lg-3">
                  <label htmlFor="">Disc Global (%)</label>
                  <input
                    name="discGlobal"
                    type="number"
                    className="form-control"
                    min={0}
                    step={0.01}
                    label="discGlobal"
                    onChange={this.discount("discGlobalPr")}
                    value={this.state.discGlobalPr}
                  />
                </div>
                <div className="col-lg-3">
                  <label htmlFor="">Disc (Gr)</label>
                  <input
                    name="discGlobal"
                    type="number"
                    className="form-control"
                    label="discGlobal"
                    value={this.state.discGlobalGr}
                    readOnly={true}
                  />
                </div>
                <div className="col-lg-3">
                  <label htmlFor="">Grand Total Netto</label>
                  <input
                    name="totalNW"
                    type="number"
                    className="form-control"
                    label="totalNW"
                    value={this.state.GrandTotalNetto}
                    readOnly={true}
                  />
                </div>
                <div className="col-lg-3">
                  <label htmlFor="">Grand Total Netto Ext</label>
                  <input
                    name="totalNWExt"
                    type="text"
                    className="form-control"
                    label="totalNWExt"
                    onChange={this.inputChange("totalNWExt")}
                    // onBlur={this.inputGrandTotal("totalNWExt")}
                  />
                </div>
                <div className="col-lg-12 col-md-12 mt-3">
                  <div className="row">
                    <div className="col-lg-6">
                      <div className="text-left">
                        <button
                          className="btn btn-warning"
                          onClick={() => this.transaksiPenjualanSubmit()}
                        >
                          Simpan
                        </button>
                      </div>
                    </div>
                    <div className="col-lg-6">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          data-toggle="modal"
                          data-target="#primary-header-modal"
                        >
                          Tambah Barang
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-lg-12 mt-5">
                  {this.props.transaksiPenjualan ? (
                    <ToolkitProvider
                      keyField="id"
                      data={this.props.transaksiPenjualan}
                      columns={this.state.columns}
                      search
                    >
                      {(props) => (
                        <div className="form-group">
                          <BootstrapTable
                            {...props.baseProps}
                            striped
                            hover
                            condensed
                            bordered={true}
                            headerClasses="header-table"
                            pagination={paginationFactory()}
                          />
                        </div>
                      )}
                    </ToolkitProvider>
                  ) : (
                    <ToolkitProvider
                      keyField="id"
                      data={[]}
                      columns={this.state.columns}
                      search
                    >
                      {(props) => (
                        <div className="form-group">
                          <BootstrapTable
                            {...props.baseProps}
                            striped
                            hover
                            condensed
                            bordered={true}
                            headerClasses="header-table"
                            pagination={paginationFactory()}
                            noDataIndication="Tidak Ada Data"
                          />
                        </div>
                      )}
                    </ToolkitProvider>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Transaksi Penjualan
                </h4>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                  id="dismissModal"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <FormTransaksiPenjualan
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

TransaksiPenjualan = reduxForm({
  form: "TransaksiPenjualan",
  enableReinitialize: true,
})(TransaksiPenjualan);
export default connect(maptostate, null)(TransaksiPenjualan);
