import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm, formValueSelector } from "redux-form";
import axios from "axios";
import { createNumberMask } from "redux-form-input-masks";
import validasiTransaksiPenjualan from "../../Validations/validasiTransaksiPenjualan";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});
const server = process.env.REACT_APP_BACKEND_URL;
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

class FormTransaksiPenjualan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      kodeBarang: [],
    };
  }

  componentDidMount() {
    axios.get(server + "subcategorys2?kodeKelompok=PERHIASAN").then((res) =>
      this.setState({
        kodeBarang: res.data,
      })
    );
  }

  onclick1() {
    this.props.change("netto", this.props.netto);
    this.props.change("noFaktur", this.props.noFaktur);
    this.props.change("gross", this.props.gross);
    this.props.change("discGlobalGr", this.props.discGlobalGr);
    this.props.change(
      "nettoDiscGlobalExt",
      (this.props.netto - this.props.discGlobalGr).toFixed(3)
    );
  }

  pilihBarang = (input) => (e) => {
    var supp = e.target.value
      ? e.target.value
      : "DEFAULT | DEFAULT | DEFAULT | DEFAULT ";
    var result_explode = supp.split("|");
    this.setState({
      kodeJenis: result_explode[0],
      namaJenis: result_explode[1],
      kadarCetak: result_explode[2],
      kadarJual: result_explode[3],
    });
  };

  setKadar() {
    this.props.change("keterangan", this.state.namaJenis);
    this.props.change("kadar", this.state.kadarCetak);
    this.props.change("kadarTransaksi", this.state.kadarJual);
  }

  setBruto() {
    this.props.change("bruto", localStorage.getItem("Timbangan"));
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-12 col-lg-12 d-none">
            <Field
              name="noFaktur"
              label={this.props.noFaktur}
              type="text"
              readOnly={true}
              component={renderField}
              defaultValue={this.props.noFaktur}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          <div className="col-md-4 col-lg-4">
            <label htmlFor="">Kode Barang</label>
            <Field
              name="kodeBarang"
              component="select"
              className="form-control"
              onChange={this.pilihBarang()}
              onBlur={this.setKadar()}
            >
              <option value="DEFAULT" key="DEFAULT">
                SILAHKAN PILIH
              </option>
              {this.state.kodeBarang === [] ? (
                <option>TIDAK ADA DATA</option>
              ) : (
                this.state.kodeBarang.map((kode) => (
                  <option
                    value={
                      kode.kodeJenis +
                      "|" +
                      kode.namaJenis +
                      "|" +
                      kode.kadarCetak +
                      "|" +
                      kode.kadarJual
                    }
                    key={kode.kodeJenis}
                  >
                    {kode.namaJenis}
                  </option>
                ))
              )}
            </Field>
          </div>
          <div className="col-md-8 col-lg-8">
            <Field
              name="keterangan"
              label="Keterangan"
              type="text"
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="kadar"
              label="Kadar (%)"
              type="number"
              component={renderField}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field name="p" label="P" type="number" component={renderField} />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field name="k" label="K" type="number" component={renderField} />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="pkg"
              label="Pkg (Gr)"
              type="number"
              component={renderField}
              onBlur={() => this.onclick1()}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="bruto"
              label="Berat (Gr)"
              type="number"
              component={renderField}
              onChange={this.onclick1()}
              // onChange={this.setBruto()}
              // onBlur={this.setBruto()}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="kadarTransaksi"
              label="Harga (%)"
              type="number"
              component={renderField}
              onBlur={() => this.onclick1()}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="netto"
              label="Netto (Gr)"
              type="number"
              component={renderField}
              readOnly={true}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="gross"
              label="Gross (Gr)"
              type="number"
              component={renderField}
              readOnly={true}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="discGlobalPr"
              label="Diskon (%)"
              type="number"
              component={renderField}
              onBlur={() => this.onclick1()}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="discGlobalGr"
              label="Diskon (Gr)"
              type="number"
              component={renderField}
              readOnly={true}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="nettoDiscGlobalExt"
              label="Netto - Diskon (Gr)"
              type="number"
              component={renderField}
              readOnly={true}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <label htmlFor="">Ongkos (Rp)</label>
            <Field
              name="rp"
              component="input"
              type="tel"
              className="form-control"
              {...currencyMask}
            />
          </div>
          <div className="col-lg-12 col-md-12">
            <div className="text-right">
              <button className="btn btn-primary" type="submit">
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormTransaksiPenjualan = reduxForm({
  form: "FormTransaksiPenjualan",
  enableReinitialize: true,
  validate: validasiTransaksiPenjualan,
})(FormTransaksiPenjualan);
const selector = formValueSelector("FormTransaksiPenjualan"); // <-- same as form name
export default connect((state) => {
  const { bruto, kadarTransaksi, pkg, discGlobalPr } = selector(
    state,
    "bruto",
    "kadarTransaksi",
    "pkg",
    "discGlobalPr"
  );
  return {
    netto: (parseFloat(bruto) * (parseFloat(kadarTransaksi) / 100)).toFixed(3),
    gross: parseFloat(bruto) + parseFloat(pkg),
    discGlobalGr: (
      parseFloat(bruto) *
      (parseFloat(discGlobalPr) / 100)
    ).toFixed(3),
  };
})(FormTransaksiPenjualan);
